.roadmap {
    margin-top: 340px;
    h2 {
      margin-top: 20px;
      margin-bottom: 30px;
      font-size: 50px;
      line-height: 40px;
      font-weight: 700;
      font-family: "Hanson", sans-serif;
      text-shadow: 0 0 12px rgb(217 216 218 / 80%);
      color: #fff;
      justify-content: center;
      display: flex;
      align-items: center;
    }
    .timeline-section {
      //background-color: #0000;
      background: url(/assets/roadmap_gg.png);
      background-position: bottom;
      background-size: cover;
      background-repeat: no-repeat;
      min-height: 100vh;
      padding: 100px 15px;
      
    }
    .timeline-items {
      max-width: 1000px;
      margin: auto;
      display: flex;
      flex-wrap: wrap;
      position: relative;
    }
    .timeline-indicator {
      content: "";
      position: absolute;
      width: 2px;
      height: 100%;
      background-color: #2f363e;
      left: calc(50% - 1px);
    }
    .timeline-progress {
      content: "";
      position: absolute;
      width: 3px !important;
      height: 100%;
      background-color: white;
      left: calc(50% - 1px);
      z-index: 2;
    }
    .timeline-item {
      margin-bottom: 40px;
      width: 100%;
      position: relative;
    }
    .timeline-item:last-child {
      margin-bottom: 0;
    }
    .timeline-item:nth-child(odd) {
      .timeline-content {
        .timeline-number {
          text-align: right;
          margin: 0 0 20px;
        }
      }
      padding-right: calc(50% + 30px);
      text-align: left;
    }
    .timeline-item:nth-child(even) {
      .timeline-content {
        .timeline-number {
          text-align: right;
          margin: 0 0 20px;
        }
      }
      padding-left: calc(50% + 30px);
    }
    .timeline-dot {
      height: 20px;
      width: 20px;
      background-color: #fff;
      position: absolute;
      left: calc(50% - 9px);
      border-radius: 50%;
      /* top: 10px; */
      box-shadow: 0 0 12px rgb(217 216 218 / 80%);
    }
    .timeline-percent {
      text-shadow: 0 0 12px rgb(217 216 218 / 80%);
      font-family: "Hanson", sans-serif;
      font-size: 25px;
      color: #fff;
      margin: 6px 0 15px 10px;
      font-weight: bold;
    }
    .timeline-content {
      box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
      //background-color: #2f363e;
      padding: 30px;
      border-radius: 15px;
      background: rgba(255, 255, 255, 0.1);
      border-top: 1px solid rgba(255, 255, 255, 0.5);
      border-left: 1px solid rgba(255, 255, 255, 0.5);
      backdrop-filter: blur(5px);
      min-height: 325px;
    }
    .timeline-content h2 {
      font-size: 25px;
      color: #fff;
      margin: 0 0 10px;
      text-transform: uppercase;
      font-weight: bold;
      text-shadow: 0 0 12px rgb(217 216 218 / 80%);
      font-family: Hanson, sans-serif;
    }
    .timeline-content h3 {
      text-shadow: 0 0 12px rgb(217 216 218 / 80%);
      font-family: Hanson, sans-serif;
      font-size: 25px;
      color: #fff;
    }
    .timeline-content p {
      color: #fff;
      font-size: 16px;
      font-weight: 300;
      line-height: 22px;
      font-weight: 500;
      margin-top: 15px;
    }
  
    /* responsive */
    @media (max-width: 767px) {
      .timeline-indicator {
        left: 7px;
      }
      .timeline-progress {
        left: 20px;
      }
      .timeline-item:nth-child(odd) {
        padding-right: 0;
        text-align: left;
      }
      .timeline-item:nth-child(odd),
      .timeline-item:nth-child(even) {
        padding-left: 37px;
      }
      .timeline-dot {
        left: -3px;
      }
    }
  }
  